//@import './Reset';

$maincoloir : #ff0000;

.wow-enter {
  opacity: 0;
}

.wow-enter-active {
  opacity: 1;
  color: red;
  transition: all 500ms;
}


@mixin hamsu(){
  background-color: red;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.my-alert{
  @include hamsu();

  p{
    margin-bottom: 0;
  }
}

.test{
  color: $maincoloir
}

.my-alert2{
  @extend .my-alert;
  background-color: yellowgreen;
}



